/** @format */

import React, { useEffect, useState } from 'react';
import { Outlet, Link, useNavigate, useLocation } from 'react-router-dom';
import { useMoralis } from 'react-moralis';
import {
  Link as LINKCH,
  HStack,
  VStack,
  Button,
  Text,
  Heading,
  useClipboard,
  Table,
  Tr,
  Td,
} from '@chakra-ui/react';
import { useMediaQuery } from 'react-responsive';
import { CopyIcon, ExternalLinkIcon } from '@chakra-ui/icons';
import * as Global from './model/global';
import * as Helper from './model/helper';
import * as STATIC from './model/static';
import * as API from './io/api';
//==========================================================================================
export default function App() {
  const isDesktop = useMediaQuery({ query: '(min-width: 1537px)' });
  const [chainId, setChainId] = useState(0);
  const [chainTitle, setChainTitle] = useState('');
  const [addr, setAddr] = useState('');
  const [did, setDid] = useState('');
  const { hasCopied, onCopy } = useClipboard(addr);
  const [web3, setWeb3] = useState({});

  const navigate = useNavigate();
  const search = useLocation().search;
  const newTerm = new URLSearchParams(search).get('term');

  const [isConnected, setIsConnected] = useState(false);

  const [isDone, setIsDone] = useState(false);

  const goSearch = async () => {
    var appState = Global.getAppState();
    if (newTerm != null && newTerm.length > 0) {
      Global.setAppstate('searchfield', newTerm);
    }
    var searchlink = '/search?term=' + appState.searchfield;
    navigate(searchlink);
  };

  const location = useLocation();

  const {
    Moralis,
    isAuthenticated,
    isAuthenticating,
    authError,
    authenticate,
    login,
    logout,
    user,
  } = useMoralis();

  // Moralis Events =================================================================
  Moralis.onConnect(async (accounts) => {
    runLogin();
  });

  Moralis.onDisconnect(async (accounts) => {
    Global.setAppstate('connected', false);
    setIsConnected(false);
  });

  Moralis.onAccountsChanged(async (accounts) => {
    if (accounts[0] == null) {
      return;
    }
    Global.loadMeByPubKey(accounts[0]);
    Global.setAppstate('did', Helper.DidFromPubkey(accounts[0]));
  });

  Moralis.onChainChanged(async (chain) => {
    if (window.ethereum) {
      var chainId = parseInt(window.ethereum.chainId, 16);
      // TODO check allowd chains...
      setChainTitle(Helper.MapChainId(chainId));
      Global.setAppstate('chainId', chainId);
    }
  });
  // End Moralis Events =================================================================

  useEffect(() => {
    // run only once
    if (isDone) {
      return;
    }
    setIsDone(true);
    if (user && Global.getAppState().token == '') {
      runLogin();
    }
  });

  const runLogin = async () => {
    const currentUser = Moralis.User.current();
    if (!currentUser) {
      return;
    }
    var address = currentUser.get('ethAddress');
    setAddr(address);
    var did = Helper.DidFromPubkey(address);
    var address;
    Moralis.enableWeb3().then((_web3) => {
      setWeb3(_web3);
      if (window.ethereum) {
        var chainId = parseInt(window.ethereum.chainId, 16);
        setChainId(chainId);
        setChainTitle(Helper.MapChainId(chainId));
        Global.setAppstate('routeky', Global.getAppState().routeky + 1);
        window.sessionStorage.setItem('sessionKey', address);
        const params = {
          username: currentUser.get('username'),
          did: did,
          session: currentUser.get('sessionToken'),
        };
        // TODO check if JWT exists and is valid in time
        Moralis.Cloud.run('createToken', params).then((token) => {
          Global.setAppstate('token', token);
          Global.loadMeByPubKey(address).then(() => {
            setIsConnected(true);
            Global.setAppstate('connected', true);
            setIsDone(false);
          });
        });
      }
    });
  };

  const LogonMetaMask = async () => {
    Moralis.authenticate().then(function (user) {
      runLogin();
    });
  };

  //https://docs.moralis.io/moralis-server/users/crypto-login
  const logonWalletConnect = async () => {
    try {
      authenticate({
        provider: 'walletconnect',
        mobileLinks: [
          'rainbow',
          'metamask',
          'argent',
          'trust',
          'imtoken',
          'pillar',
        ],
        chainId: 43113,
      }).then(function (user) {
        Moralis.Web3.enable({
          provider: 'walletconntect',
          chainId: 43113,
        }).then(function (web3) {
          runLogin();
        });
      });
    } catch (err) {
      console.log(err);
    }
  };

  const logoutAll = async () => {
    Global.setAppstate('token', '');
    window.sessionStorage.setItem('sessionKey', '');
    setIsConnected(false);
    Global.setAppstate('connected', false);
    logout();
  };

  var blokexplorerAccount = 'https://testnet.snowtrace.io/address/' + addr;

  const linkSelected = () => {
    var addr = user.get('ethAddress');
    var did = Helper.DidFromPubkey(addr);
    var viewlink = '/show/' + did;
    navigate(viewlink);
  };

  const viewSelected = () => {
    if (Global.getAppState().did) {
      var linkShowLInk = '/show/' + Global.getAppState().did;
      navigate(linkShowLInk);
      return;
    }
    navigate('/search?term=' + Global.getAppState().searchfield);
  };

  if (!isDesktop) {
    return (
      <div
        style={{
          padding: '0',
          margin: '0',
          width: '100%',
          top: '0px',
          position: 'absolute',
        }}>
        <nav>
          <center>
            <Table
              size="lg"
              variant="simple"
              style={{
                borderSpacing: '0px',
                p: '0',
                m: '0',
                width: '100%',
              }}>
              <Tr>
                <Td width="100%">
                  <center>
                    <Heading fontSize="3xl">youfoundme.io</Heading>
                  </center>
                </Td>
              </Tr>
              <Tr>
                <Td width="100%">
                  <center>
                    {!isConnected && (
                      <VStack>
                        <HStack>
                          <Button
                            isLoading={isAuthenticating}
                            onClick={() => logonWalletConnect()}>
                            Wallet Connect
                          </Button>
                          &nbsp;&nbsp;
                          <Button
                            isLoading={isAuthenticating}
                            onClick={() => LogonMetaMask()}>
                            Metamask
                          </Button>
                        </HStack>
                      </VStack>
                    )}

                    {isConnected && (
                      <VStack>
                        <HStack>
                          <Button onClick={() => logoutAll()}>disconnet</Button>
                          <LINKCH
                            color="blue.500"
                            href="https://testnet.snowtrace.io/block/0x3b9197c2e27818ab344d1c567917e8c569850b5901926f1e777c0e505547a84e"
                            isExternal>
                            <HStack align="flex-end">
                              <Text fontSize="xs">
                                {chainTitle} {chainId}
                              </Text>
                            </HStack>
                          </LINKCH>
                        </HStack>

                        <HStack>
                          <LINKCH onClick={() => linkSelected()}>
                            <Text color="blue.500" fontSize="xs">
                              me
                            </Text>
                          </LINKCH>
                          <LINKCH href={blokexplorerAccount} isExternal>
                            <HStack>
                              <Text fontSize="xs">{addr}</Text>
                              <ExternalLinkIcon
                                fontSize="xs"
                                style={{
                                  marginBottom: '4px',
                                  marginLeft: '2px',
                                }}
                              />
                            </HStack>
                          </LINKCH>
                        </HStack>
                      </VStack>
                    )}
                  </center>
                </Td>
              </Tr>

              <Tr>
                <Td width="100%">
                  <center>
                    <Button onClick={goSearch}>Find</Button>
                    &nbsp;&nbsp;
                    <Button onClick={() => viewSelected()}>View</Button>
                    &nbsp;&nbsp;
                    <Link to="/editor">
                      <Button>Me</Button>
                    </Link>
                  </center>
                </Td>
              </Tr>
              <Tr>
                <Td>
                  <Outlet />
                </Td>
              </Tr>
              <Tr>
                <Td>
                  <center>
                    <Text color="green.500" fontSize="xs">
                      {STATIC.VERSION_YEAR} TECH41 GmbH Berlin - Privacy Policy
                      - Terms of Service
                    </Text>
                    <Text color="green.500" fontSize="xs">
                      V: {STATIC.VERSION}
                    </Text>
                  </center>
                </Td>
              </Tr>
            </Table>
          </center>
        </nav>
      </div>
    );
  }

  return (
    <div
      style={{
        padding: '0',
        margin: '0',
        width: '100%',
        top: '0px',
        position: 'absolute',
      }}>
      <nav>
        <center>
          <Table
            size="lg"
            variant="simple"
            style={{
              borderSpacing: '0px',
              p: '0',
              m: '0',
              width: '100%',
            }}>
            <Tr>
              <Td
                style={{
                  width: '20px',
                }}>
                <Heading fontSize="3xl">youfoundme.io</Heading>
              </Td>
              <Td width="100%">
                <center>
                  <nav
                    style={{
                      width: '100%',
                      borderBottom: 'solid 0px',
                      paddingBottom: '1rem',
                    }}>
                    <Button onClick={goSearch}>Find</Button>
                    &nbsp;&nbsp;
                    <Button onClick={() => viewSelected()}>View</Button>
                    &nbsp;&nbsp;
                    <Link to="/editor">
                      <Button>Me</Button>
                    </Link>
                  </nav>
                </center>
              </Td>
              <Td width="20px" align="right">
                <VStack align="flex-end">
                  {!isConnected && (
                    <HStack align="flex-end">
                      <Button
                        isLoading={isAuthenticating}
                        onClick={() => logonWalletConnect()}>
                        Wallet Connect
                      </Button>
                      <Button
                        isLoading={isAuthenticating}
                        onClick={() => LogonMetaMask()}>
                        Metamask
                      </Button>
                    </HStack>
                  )}
                  {isConnected && (
                    <VStack align="flex-end">
                      <HStack align="flex-end">
                        <LINKCH
                          color="blue.500"
                          href="https://testnet.snowtrace.io/block/0x3b9197c2e27818ab344d1c567917e8c569850b5901926f1e777c0e505547a84e"
                          isExternal>
                          <HStack align="flex-end">
                            <Text fontSize="xs">
                              {chainTitle} {chainId}
                            </Text>
                          </HStack>
                        </LINKCH>
                        <Button onClick={() => logoutAll()}>disconnet</Button>
                      </HStack>

                      <HStack align="flex-end">
                        <LINKCH onClick={() => linkSelected()}>
                          <Text color="blue.500" fontSize="xs">
                            me
                          </Text>
                        </LINKCH>
                        <LINKCH href={blokexplorerAccount} isExternal>
                          <HStack align="flex-end">
                            <Text fontSize="xs">{addr}</Text>
                            <ExternalLinkIcon
                              fontSize="xs"
                              style={{ marginBottom: '4px', marginLeft: '2px' }}
                            />
                          </HStack>
                        </LINKCH>
                      </HStack>
                    </VStack>
                  )}
                  ,
                </VStack>
              </Td>
            </Tr>
            <Tr>
              <Td colspan="3">
                <Outlet />
              </Td>
            </Tr>
            <Tr>
              <Td colspan="3">
                <center>
                  <Text color="green.500" fontSize="xs">
                    {STATIC.VERSION_YEAR} TECH41 GmbH Berlin - Privacy Policy -
                    Terms of Service - V: {STATIC.VERSION}
                  </Text>
                </center>
              </Td>
            </Tr>
          </Table>
        </center>
      </nav>
    </div>
  );
}
