/** @format */

import React, { useState, useEffect } from 'react';
import { useNavigate, useParams, useLocation } from 'react-router-dom';

import {
  Heading,
  Image,
  Input,
  Container,
  Text,
  Button,
  HStack,
  Link,
  Table,
  Tr,
  Td,
  Img,
  Header,
  VStack,
} from '@chakra-ui/react';

import { getAppState, loadPersonByPubKey, setAppstate } from '../model/global';
import { searchPerson, getPerson } from '../io/api';
import * as Global from '../model/global';
import * as Helper from '../model/helper';
import * as STATIC from '../model/static';

const Search = () => {
  const [searchResult, setSearchResult] = useState([]);
  const [searchfield, setSearchfield] = useState('');
  const [flags, setFlags] = useState(0);
  const search = useLocation().search;
  const newTerm = new URLSearchParams(search).get('term');

  useEffect(() => {
    var appState = Global.getAppState();
    if (appState.searchfield != '') {
      setSearchfield(appState.searchfield);
    }

    if (searchResult.length == '' && appState.searchResult.length > 0) {
      setSearchResult(JSON.parse(appState.searchResult));
    }
  });

  const handleChange = (e) => {
    var search = e.target.value;
    setSearchfield(search);
    Global.setAppstate('searchfield', search);
  };

  const navigate = useNavigate();

  //=============================================

  let appState = getAppState();

  const startSearch = async () => {
    var searchterm = searchfield.trim();
    if (searchfield.length == 0) {
      setSearchfield('all');
      searchterm = 'all';
    }

    if (searchfield.startsWith('did:yfm:')) {
      var pubkey = Helper.PubkeyFromDid(searchfield);
      var person = await getPerson(pubkey);
      if (JSON.stringify(person).indexOf('error') > 0) {
        setSearchResult([]);
        Global.setAppstate('searchResult', JSON.stringify([]));
      } else {
        navigate('/show/' + searchfield);
      }
      return;
    }

    Global.setAppstate('searchfield', searchterm);
    var people = await searchPerson(searchterm);
    setSearchResult(people);
    Global.setAppstate('searchResult', JSON.stringify(people));
    navigate('/search?term=' + searchterm);
  };

  const linkSelected = (did) => {
    var linkShowLInk = '/show/' + did;
    Global.setAppstate('did', did);
    navigate(linkShowLInk);
  };

  if (searchResult == null || searchResult.length == 0) {
    return (
      <main style={{ padding: '1rem 0' }}>
        <Container>
          <Heading>Search</Heading>

          <HStack>
            <Input
              placeholder="Keywords, Username, DID or Tags, blank shows new members"
              onChange={handleChange}
              value={searchfield}
            />
            <Button onClick={startSearch}>go</Button>
          </HStack>
          <br />
          <Text color="teal.500" fontSize="xs">
            No records found
          </Text>
        </Container>
      </main>
    );
  }
  return (
    <main style={{ padding: '1rem 0' }}>
      <Container>
        <Heading>Search</Heading>
        <HStack>
          <Input
            placeholder="Keywords, Username, DID or Tags, blank shows new members"
            onChange={handleChange}
            value={searchfield}
          />
          <Button onClick={startSearch}>go</Button>
        </HStack>
        <br />
        <Text color="teal.500" fontSize="xs">
          found: {searchResult.length}
        </Text>
        <Table variant="simple" width="100%" border="1" padding="0" margin="0">
          {Object.keys(searchResult).map((k, i) => {
            let d = searchResult[k];
            let fl = parseInt(d.flags);
            return (
              <Tr key={i}>
                <Td wdith="300px">
                  <Link onClick={() => linkSelected(d.did)}>
                    <Image
                      width="300px"
                      height="300px"
                      boxSize="150px"
                      objectFit="cover"
                      src={d.img}
                      alt={d.username}
                    />
                  </Link>
                </Td>
                <Td width="50%">
                  <Table border="0" padding="0" margin="0">
                    <Tr>
                      <Td>
                        <Link onClick={() => linkSelected(d.did)}>
                          <Heading>{d.username}</Heading>
                        </Link>
                      </Td>
                    </Tr>
                    <Tr>
                      <Td>
                        <Link onClick={() => linkSelected(d.did)}>
                          {d.jobtitle}
                        </Link>
                      </Td>
                    </Tr>
                    <Tr>
                      <Td>
                        <Link onClick={() => linkSelected(d.did)}>
                          {d.teaser}
                        </Link>
                      </Td>
                    </Tr>
                    <Tr>
                      <Td colspan="2">
                        <HStack>
                          {fl & STATIC.FLA_FULLTIME && (
                            <Text color="blue.500" fontSize="xs">
                              full time |
                            </Text>
                          )}

                          {fl & STATIC.FLA_PARTTIME && (
                            <Text color="blue.500" fontSize="xs">
                              part time |
                            </Text>
                          )}

                          {fl & STATIC.FLA_CONTRACTS && (
                            <Text color="blue.500" fontSize="xs">
                              contract |
                            </Text>
                          )}

                          {fl & STATIC.FLA_PROJECTS && (
                            <Text color="blue.500" fontSize="xs">
                              projects |
                            </Text>
                          )}

                          {fl & STATIC.FLA_INVESTOR && (
                            <Text color="blue.500" fontSize="xs">
                              investor |
                            </Text>
                          )}
                          {fl & STATIC.FLA_STUDENT && (
                            <Text color="blue.500" fontSize="xs">
                              student |
                            </Text>
                          )}
                          {fl & STATIC.FLA_SCHOOL && (
                            <Text color="blue.500" fontSize="xs">
                              school |
                            </Text>
                          )}
                        </HStack>
                      </Td>
                    </Tr>
                  </Table>
                </Td>
              </Tr>
            );
          })}
        </Table>
      </Container>
    </main>
  );
};

export default Search;
